import IFormStructure, { ITabDefinition, IFieldDefinition } from "../Model/IFormStructure";
import { unwatchFile } from "fs";
import FormStructure from "../Model/FormStructure";
import { keyframes } from "office-ui-fabric-react";

export default class TabRules{


    

    runRules(model:object, formStructure:IFormStructure ):IFormStructure{ 
        
        //2. Overview / Mission identity / Field: Extra MS monitored (3 Field repeated for each extra MS agreeing to be monitored.) 
        let check2 = (model:any, structure:IFormStructure)=>{
            let exisitingExtraTabs = formStructure["Overview"].filter(x=>{return x.tab.indexOf('Extra MS monitored #')==0;});
            return  (model as any)["Extra MS monitored"]!=null &&
                    (model as any)["Extra MS monitored"].length != exisitingExtraTabs.length;
        };        
        formStructure = this.setExtraMSRulesTabs(check2,formStructure, model);


        //16. Return-Flight / Returning returnees (Omit if type = CRO)
        let check16 = (model:any)=>{ return (model as any)["Operation type"]!=null && (model as any)["Operation type"] === 'CRO'; };
        formStructure = this.hideIf(check16, "Return-flight","Returning returnees",  formStructure, model);

        return formStructure;
    }

    
    private hideIf(condition:(model:object)=>boolean, navLabel:string, tabLabelToHide:string, structure:IFormStructure, model:object ):IFormStructure{
        if(condition(model) && 
        (structure as any)[navLabel]!=null){

            let nav = (structure as any)[navLabel];
            for(let i=0;i<nav.length;i++){
                if(nav[i].tab == tabLabelToHide){
                    nav[i].isHidden=true;
                    break;
                }
            }

            let newStructure:any = structure;
            newStructure[navLabel] = nav;
            structure = newStructure;

        }

        return structure;
    }

    private setExtraMSRulesTabs(condition:(model:object, formStructure:IFormStructure)=>boolean, structure:IFormStructure, model:object ){
        let modelLabel = "Extra MS monitored";
        let tabLabel = "Extra MS monitored #";

        if(condition(model,structure) && (model as any)[modelLabel]!=null){
            //remove all extra ms tabs
            structure = this.removeTabs(structure, model, "Overview", tabLabel);
            let clearStructure:IFormStructure = (new FormStructure()).formStructure;
            let tabDefinition =  clearStructure["Overview"].filter(t=>{return t.tab === "MS monitored"})[0];
            //recreate extra ms tabs
            let count = (model as any)[modelLabel].length;
            for(let i=0;i<count;i++){
                let td:ITabDefinition = JSON.parse(JSON.stringify(tabDefinition));

                td.fields = td.fields.map((f:IFieldDefinition)=>{
                    f.value=null;
                    f.internalName = f.internalName+' #'+(i+1);
                    return f;
                });

                td.tab = tabLabel+(i+1);
                structure = this.insertTab(structure,"Overview","MS monitored", td);
            }

            //for all Tabs of 'Extra MS monitored #' over possible count model should be cleared
            let clearFields = clearStructure["Overview"].filter(t=>{return t.tab === "MS monitored"})[0].fields;

            clearFields.forEach((field:IFieldDefinition)=>{                
                Object.keys(model).forEach((modelKey)=>{
                    if( modelKey.indexOf(field.internalName) == 0 && 
                        modelKey.indexOf('#')!=-1 &&
                        parseInt(modelKey.split('#')[1])>count){
                            let nm = model as any;
                            delete nm[modelKey];
                            model = nm;  //This clearly works only beacause of parametrizing model as reference. A little bit hacky;
                    }
                });
            });
        }

        return structure;
    }

    private removeTabs(structure:IFormStructure, model:object, nav:string, labelStartsWith:string){
        let newStructure = (structure as any);
        let newTabs = newStructure[nav];
        newTabs = newTabs.filter((t:ITabDefinition)=>{return t.tab.indexOf(labelStartsWith)!=0});
        newStructure[nav]=newTabs;
        structure=newStructure;
        return structure;
    }

    private insertTab(structure:IFormStructure, nav:string, lastLabelContains:string, tabDefinition:ITabDefinition){
        let newStructure = (structure as any);
        let newTabs = newStructure[nav];
        //get tabAfter
        let index = 0;
        for(let i=0;i<newTabs.length;i++){
            if(newTabs[i].tab.indexOf(lastLabelContains)!=-1) index=i;
        }

        //insert
        newTabs.splice(index+1, 0, tabDefinition);

        newStructure[nav]=newTabs;
        structure=newStructure;        
        return structure;
    }

}