import { ReactComponent } from "*.svg";
import React from "react";
import { Stack, TextField, PrimaryButton, Button, DefaultButton, Text, Nav, INavLink, Pivot, PivotItem, Label, FontIcon, IconButton, Icon, TooltipHost, ProgressIndicator, Dialog, DialogType } from "office-ui-fabric-react";

import styles from './../formstyles.module.css'; 
import IFormStructure, { ITabDefinition, IFieldDefinition, FieldType } from "./../Model/IFormStructure";
import HeadingRow from "./HeadingRow";
import IIncident from "../Model/IIncident";
import OptionsFieldRow from "./OptionsRow";
import LongTextFieldRow from "./LongTextFieldRow";
import FormStructure from "../Model/FormStructure";

export default class IncidentsRow extends React.Component<IFieldDefinition,any>{ 

    constructor(props:IFieldDefinition){
        super(props);

        this.state = {
            value: this.props.value ? this.props.value : null,
            incidents: this.props.value ? this.props.value : []
        };

        this.setStateValue = this.setStateValue.bind(this);
        this.setValue = this.setValue.bind(this);
        this.addIncident = this.addIncident.bind(this);
        this.setIncidentValue = this.setIncidentValue.bind(this);
    }

    componentWillReceiveProps(prevProps:IFieldDefinition){
        //TODO: reloading data
        let newState = {
            value: prevProps.value ? prevProps.value : null,
            incidents: this.props.value ? this.props.value : []
        };
        this.setState(newState, this.forceUpdate);
    }

    setStateValue(fieldName:string, value:any){
        let ns = this.state as any;
        ns[fieldName] = value;
        this.setState(ns, this.forceUpdate);
    }

    setIncidentValue(fieldName:string, value:any, index:number){
        let ns = this.state.incidents as any;
        ns[index][fieldName] = value;
        this.setState({incidents:ns}, this.forceUpdate);

        this.setValue(ns);
    }
    
    setValue(value:any){
        let ns = this.state as any;
        ns.value = value; 
        this.setState(ns, this.forceUpdate);

        if(this.props.onChanged)
            this.props.onChanged(value);
    }

    addIncident(){
        let inc = this.state.incidents;
        inc.push(new IIncident());
        this.setState({incidents:inc}, this.forceUpdate);
        this.setValue(inc);
    }

    removeIncident(index:number){
        let inc = this.state.incidents;
        inc.splice(index,1);

        this.setState({incidents:inc}, this.forceUpdate);
        this.setValue(inc);
    }

    public render(): React.ReactElement<any> {
        return (
            <div className={"ms-Grid-row "+styles.fieldRow}>                     
                {this.state.incidents.map((i:IIncident, index:number)=>{
                    return <div className={"ms-Grid-col ms-md12 ms-sm12"} 
                        key={'incident_'+index} >
                        <HeadingRow 
                            label={"Incident #"+(index+1)} 
                            internalName="incidentHeading" 
                            isHeading={true} 
                            isRequired={false} 
                            hasInfo={false} 
                            type={FieldType.heading}/>
                        <OptionsFieldRow 
                            label="MS contingent(s) involved" 
                            internalName="MS contingent(s) involved" 
                            value={this.state.incidents[index]["MS contingent(s) involved"]}
                            onChanged={((v:any)=>{this.setIncidentValue("MS contingent(s) involved",v,index);}).bind(this)}
                            options={FormStructure.countries} 
                            isRequired={true} 
                            hasInfo={true} 
                            info={"@TODO"} 
                            type={FieldType.dropdown} />
                        <LongTextFieldRow 
                            label="Description of incident" 
                            internalName="Description of incident" 
                            value={this.state.incidents[index]["Description of incident"]}
                            onChanged={((v:any)=>{this.setIncidentValue("Description of incident",v,index);}).bind(this)}
                            isRequired={true}  
                            hasInfo={true} 
                            info={"@TODO"} 
                            type={FieldType.longtext} />
                        <OptionsFieldRow 
                            label="Coercive measures?" 
                            internalName="Coercive measures?" 
                            value={this.state.incidents[index]["Coercive measures?"]}
                            onChanged={((v:any)=>{this.setIncidentValue("Coercive measures?",v,index);}).bind(this)}
                            options={FormStructure.countries} 
                            isRequired={true} 
                            hasInfo={true} 
                            info={"@TODO"} 
                            type={FieldType.dropdown} />      
                        <div className={"ms-Grid-row "+styles.fieldRow}>           
                            <div className={"ms-Grid-col ms-sm12 ms-md3"}>
                                <DefaultButton   
                                    iconProps={{iconName:"Cancel"}}
                                    text="Remove incident"
                                    onClick={(()=>this.removeIncident(index)).bind(this)}
                                />
                            </div>
                        </div>
                    </div>;
                })}      
                <div className={"ms-Grid-col ms-md11 ms-sm12 "+styles.textRight}>
                    {this.state.incidents.length==0 &&
                    <Text className={styles.centerText}>No incidents occured.</Text>}
                    <br/>
                    {this.state.incidents.length>0 &&
                    <hr/>}
                    <PrimaryButton iconProps={{iconName:"Add"}} text="Add incident" onClick={this.addIncident}/> 
                </div>
            </div>
        );
    }
}