import IFormStructure from "../Model/IFormStructure";

export default class FormRulesStructure{

    runRules(model:object, structure: IFormStructure):IFormStructure{
    
        //9. Overview / Other states and participants section: Teams of other states (3 If Type = NRO then this section is absent.)
        let check9 = (model:any)=>{ return (model as any)["Operation type"]!=null && (model as any)["Operation type"]== 'NRO'; };
        structure = this.hideIf(check9, "Teams of other states",  model, structure, "Overview","Other states and participants");
        structure = this.hideIf(check9, "Other state personnel",  model, structure, "Overview","Other states and participants");
        structure = this.hideIf(check9, "Monitors of other states",  model, structure, "Overview","Other states and participants");
        
        //10. Pre-departure / Transfer, embarkation / Field: Scheduled transport (1 Only present if Type = Scheduled.) -
        let check10 = (model:any)=>{ return (model as any)["Operation type"]!=null && (model as any)["Operation type"]!= 'Scheduled'; };
        structure = this.hideIf(check10, "Scheduled transport",  model, structure, "Pre-departure","Transfer, embarkation");

        return structure;
    }


    private hideIf(condition:(model:object)=>boolean, fieldInternalToHide:string, model:object, structure: IFormStructure, navLabel:string, tabLabel:string){


        if(condition(model) && 
            (structure as any)[navLabel]!=null){            
            
            let nav = (structure as any)[navLabel];

            for(let i=0;i<nav.length;i++){
                if(nav[i].tab == tabLabel){
                    for(let l=0;l<nav[i].fields.length;l++){
                        if(nav[i].fields[l].internalName === fieldInternalToHide){
                            nav[i].fields[l].isHidden = true;
                            break;
                        }
                    }
                    break;
                }
            } 

            let newStructure:any = structure;
            newStructure[navLabel] = nav;
            structure = newStructure;
        }

        return structure;
    }

}