import { ReactComponent } from "*.svg";
import React from "react";
import { Stack, TextField, PrimaryButton, Button, DefaultButton, Text, Nav, INavLink, Pivot, PivotItem, Label, FontIcon, IconButton, Icon, TooltipHost, ProgressIndicator, Dialog, DialogType } from "office-ui-fabric-react";

import styles from './formstyles.module.css';
import FormStructure from "./Model/FormStructure";
import IFormState from "./IFormState";
import IFormStructure, { ITabDefinition, IFieldDefinition, FieldType } from "./Model/IFormStructure";
import TextFieldRow from "./Fields/TextFieldRow";
import OptionsFieldRow from "./Fields/OptionsRow";
import DateFieldRow from "./Fields/DateFieldRow";
import DateTimeFieldRow from "./Fields/DateTimeFieldRow";
import NumberFieldRow from "./Fields/NumberFieldRow";
import LongTextFieldRow from "./Fields/LongTextFieldRow";
import HeadingRow from "./Fields/HeadingRow";
import DownloadTextFieldRow from "./Fields/DownloadTextField";
import FormRules from "./Rules/FormRules";
import TabRules from "./Rules/TabRules";
import NavRules from "./Rules/NavRules";
import IncidentsRow from "./Fields/IncidentsRow";
import IIncident from "./Model/IIncident";
import FormRulesStructure from "./Rules/FormRulesStructure";
import ExtraMSMonitoredRow from "./Fields/ExtraMSMonitoredRow";



export default class Form extends React.Component<any,IFormState>{ 

    apiAddress = "https://www.google.com/"; //"https://jsonplaceholder.typicode.com/posts";
    apiAddressIsLive = "https://www.google.com/"; //"https://jsonplaceholder.typicode.com";

    formStructure:IFormStructure = (new FormStructure()).formStructure;
    formRules:FormRules = new FormRules();
    formRulesStructure:FormRulesStructure = new FormRulesStructure();
    tabRules:TabRules = new TabRules();
    navRules:NavRules = new NavRules();


    formRefs={};

    constructor(props:any){
        super(props);


        let defaultNav = Object.keys(this.formStructure)[0];
        let defaultTab =  (this.formStructure  as any)[defaultNav][0].tab;

        this.state={  
            awaitingRequests:0,
            navDefinition: [{
                links: Object.keys(this.formStructure).map(fs=>{
                    return {
                        name: fs,
                        url: '',
                        key: fs,
                        target: '_blank'
                    }
                }) 
            }],
            selectedNav: defaultNav,
            selectedTab: defaultTab,
            showRequestOffline:false,
            showRequestSentDialog:false,
            showRequestOfflineSent:false,
            showPleaseWait:false,
            showValidationDialog:false,
            failedValidations:[],
            model:{},
        }; 


        this.onSubmit = this.onSubmit.bind(this);
        this.onCancel = this.onCancel.bind(this);
        this.setValue = this.setValue.bind(this); 
        this.trySendAgain = this.trySendAgain.bind(this);
        this.onNavClick = this.onNavClick.bind(this);
        this.onTabClick = this.onTabClick.bind(this);
        this.getFormForTab = this.getFormForTab.bind(this);
        this.getFieldRow = this.getFieldRow.bind(this);
        this.onSaveDraft = this.onSaveDraft.bind(this);
        this.onBackToDraft = this.onBackToDraft.bind(this);
        this.runRules = this.runRules.bind(this);

        setTimeout(this.trySendAgain, 1000); //first run
        setInterval(this.trySendAgain, 30000);
    }    

    trySendAgain(){
        let offlineRequests = localStorage.getItem("offlineRequests");
        if(offlineRequests!=null && offlineRequests != ""){

            let offlineParsed = JSON.parse(offlineRequests);
            this.setState({awaitingRequests:offlineParsed.length}, this.forceUpdate);

            let requestsArray:any[] = offlineParsed;
            let requestsCopy = requestsArray;
            
            fetch(this.apiAddressIsLive).then(ready=>{                            
                requestsArray.forEach((e, i) => {
                    fetch(this.apiAddress, {
                        method: "GET", //"POST",
                        //body: JSON.stringify(e), //Uncomment
                    }).then(success=>{
                        console.log('success',success); 
                        this.setState({showRequestOfflineSent:true},this.forceUpdate);
                        requestsCopy.splice(i,1);
                        localStorage.setItem("offlineRequests", JSON.stringify(requestsCopy))
                        this.setState({awaitingRequests:requestsCopy.length});
                    });
                });
            })
        }
    }

    onCancel(){         
        localStorage.setItem("draftRequest", "");
        this.formStructure = (new FormStructure()).formStructure;
        this.setState({ 
            model:{},
        }, this.forceUpdate);
    }

    onBackToDraft(){
        let modelFromStorage = localStorage.getItem("offlineRequests");
        localStorage.setItem("offlineRequests","");
        if(modelFromStorage && modelFromStorage.length>0){
            let model = JSON.parse(modelFromStorage)[0];
            this.setState({
                model:model,
                awaitingRequests:0,
            }, this.forceUpdate);            
        }
    }

    onSubmit(){
        console.log(this.state);
        this.setState({
            showPleaseWait:false,
            failedValidations:[],
            navDefinition: [{
                links: Object.keys(this.formStructure).map(fs=>{
                    return {
                        name: fs,
                        url: '',
                        key: fs,
                        target: '_blank'
                    }
                }) 
            }],
        }, this.forceUpdate);

        let isValid=true;
        let requiredEmptyFields = Array<any>();

        //Navs
        Object.keys(this.formStructure).forEach(navKey => {
            let nav = (this.formStructure as any)[navKey];
            //Tabs
            nav.forEach((tab:ITabDefinition)=>{
                //Fields
                tab.fields.forEach((f:IFieldDefinition)=>{

                    if(f.isRequired && !f.isHidden && !f.disbaled && (this.state.model[f.internalName] == null || this.state.model[f.internalName] == "")) {
                        isValid=false;
                        requiredEmptyFields.push({
                            nav:navKey,
                            tab:tab.tab,
                            field:f.label
                        });                    
                    }
                });
            })            
        });

        //Validating incidents
        if(this.state.model["Incidents"] && this.state.model["Incidents"].length>0){
            this.state.model["Incidents"].forEach(((i:IIncident, index:number)=>{
                if( i["MS contingent(s) involved"] == null ||
                    i["Coercive measures"] != null){
                    isValid=false;
                    requiredEmptyFields.push({
                        nav:"Incident(s)",
                        tab:"Incident(s)",
                        field:"Incident #"+(index+1)
                    });     
                }
            }));
        }

        if(!isValid){
            this.setState({
                showValidationDialog:true,
                showPleaseWait:false,
                failedValidations: requiredEmptyFields,                
                navDefinition: [{
                    links: Object.keys(this.formStructure).map(fs=>{
                        return {
                            name: fs,
                            url: '',
                            key: fs,
                            target: '_blank',
                            icon: (requiredEmptyFields.filter(x=>{return x.nav == fs}).length > 0 ? "StatusErrorFull" : undefined)
                        }
                    }) 
                }],
            }, this.forceUpdate);            
        } else {
            
            this.setState({
                showPleaseWait:false,                
            }, this.forceUpdate);

            fetch(this.apiAddress, {
                method: "GET", //POST
                //body: JSON.stringify(this.state.model), //Uncomment
            }).then(success=>{
                localStorage.setItem("draftRequest", "");
                this.setState({showPleaseWait:false}, this.forceUpdate);
                console.log('success',success); 
                this.setState({
                    showRequestSentDialog: true,
                    model:{}
                }, this.forceUpdate);
            }).catch(failure=>{
                this.setState({
                    showPleaseWait:false
                }, this.forceUpdate);
                console.log('failure',failure);
                
                let offlineRequests = localStorage.getItem("offlineRequests");
                let newOfflineRequests = null;

                if(offlineRequests == null || offlineRequests == "" ){ 
                    newOfflineRequests = [this.state.model]; 
                } else {
                    newOfflineRequests = JSON.parse(offlineRequests);
                    newOfflineRequests.push(this.state.model);
                }  
                localStorage.setItem("offlineRequests",JSON.stringify(newOfflineRequests));
                this.setState({
                    awaitingRequests:newOfflineRequests.length,
                    showRequestOffline:true,
                    model:{}
                });
            });
        }
    }

    onSaveDraft(){        
        localStorage.setItem("draftRequest", JSON.stringify(this.state.model));
    }

    onDownloadData(data:any){
        //update fields abd staff
        this.onSaveDraft();
    }

    componentDidMount() {        
        //Navs
        Object.keys(this.formStructure).forEach(navKey => {
            let nav = (this.formStructure as any)[navKey];
            //Tabs
            nav.forEach((tab:ITabDefinition)=>{
                //Fields
                tab.fields.forEach((f:IFieldDefinition)=>{
                    (this.formRefs as any)[f.internalName]=React.createRef();
                });
            })
            
        });

        let draft = localStorage.getItem("draftRequest");
        if(draft!=null && draft!="")
            this.setState({model:JSON.parse(draft)}, this.runRules);
        

    }


    setValue(fieldName:string, value:any){
        let ns = this.state as any;
        ns[fieldName] = value;
        this.setState(ns, this.forceUpdate);
    }

    onModelChanged(label:string, value:string){      
        let newState = this.state;         
        newState.model[label] = value;
        this.setState(newState, ()=>{
            this.runRules(newState);
        });        
    }

    runRules(newState?:any){
        if(newState==null) newState = this.state;

        let model = newState.model;
        
        this.formStructure = this.formRulesStructure.runRules(model,this.formStructure);
        this.formStructure = this.navRules.runRules(model,this.formStructure);
        this.formStructure = this.tabRules.runRules(model,this.formStructure); 

        model = this.formRules.runRules(model);

        this.forceUpdate()
    }

    onNavClick(ev: React.MouseEvent<HTMLElement> | undefined, item?: INavLink | undefined){
        if(item){ 
            this.setState({
                selectedNav : item.name, 
                selectedTab : "",
            }, ()=>{
                this.forceUpdate();            
                this.onTabClick({props:{itemKey:(this.formStructure  as any)[item.name][0].tab}} as PivotItem);
            });

        }
    }

    onTabClick(item?: PivotItem, ev?: React.MouseEvent<HTMLElement>){
        if(item && item.props.itemKey){
            console.log(item);
            this.setState({selectedTab: item.props.itemKey}, this.forceUpdate);
        }
    }

    public render(): React.ReactElement<any> {
        return (
            <div className={styles.formStyles}>
                <div className="ms-Grid" dir="ltr" >
                    <div className="ms-Grid-row">
                        <div className={"ms-Grid-col ms-sm12 ms-md3 ms-lg2"}>
                            <div className={styles.companyLogo}></div>                            
                        </div>
                        <div className={"ms-Grid-col ms-sm12 ms-md9 ms-lg10 "+styles.headerTextParent}>
                            <span className={styles.headerText} >FREM III</span>
                        </div>
                    </div>
                    {this.state.awaitingRequests == 0 &&
                    <div className={"ms-Grid-row "+styles.content}>
                        <div className="ms-Grid-col ms-sm12 ms-md4 ms-lg3 ms-xl2">
                            <Nav 
                                selectedKey={this.state.selectedNav}
                                onLinkClick={this.onNavClick}
                                groups={this.state.navDefinition}
                                styles={{
                                    root: { 
                                        boxSizing: 'border-box',
                                        border: '1px solid #eee'
                                    }
                                }}
                            />
                        </div>
                        <div className="ms-Grid-col ms-sm12 ms-md8 ms-lg9 ms-xl10">
                            {this.state.selectedTab && this.state.selectedTab!= "" && 
                            <Pivot
                                defaultSelectedIndex={0} 
                                className={styles.msPivotFix}
                                selectedKey={this.state.selectedTab}
                                onLinkClick={this.onTabClick}                                                                                                
                                >
                                {(this.formStructure as any)[this.state.selectedNav]
                                    .filter((tab:ITabDefinition)=>{return !tab.isHidden})
                                    .map((tab:ITabDefinition)=>{
                                    return (<PivotItem 
                                                headerText={tab.tab} 
                                                key={tab.tab}                                                 
                                                itemIcon={this.state.failedValidations.filter(x=>x.tab == tab.tab).length>0 ? "StatusErrorFull" : undefined}>
                                                {this.getFormForTab(tab.fields, tab.tab, this.state.selectedNav)}
                                            </PivotItem>);
                                })} 
                            </Pivot>}                       
                        </div>
                    </div>}
                    <div className="ms-Grid-row">  
                        {this.state.awaitingRequests == 0 &&  
                        <div className={"ms-Grid-col ms-sm12 ms-md12 "+styles.centerText} >             
                            <ProgressIndicator label={"No awaiting offline requests."} percentComplete={0}/>  
                        </div>} 
                        {this.state.awaitingRequests!=undefined && this.state.awaitingRequests > 0 &&  
                        <div className={"ms-Grid-col ms-sm12 ms-md12 "+styles.centerText} >   
                            <br/>    
                            <br/>          
                            <ProgressIndicator label={"Offline request is awaiting for internet connection."} />                                                          
                            <PrimaryButton         
                                text="Back to draft" 
                                allowDisabledFocus
                                onClick={this.onBackToDraft} />  
                        </div>}
                    </div>
                </div>     
                {this.state.showRequestSentDialog &&
                <Dialog
                    hidden={!this.state.showRequestSentDialog}
                    title="Form has been saved"
                    onDismiss={()=>{this.setValue("showRequestSentDialog", false)}}
                >
                    Your form has been successfully saved.
                </Dialog> }
                {this.state.showRequestOffline &&
                <Dialog
                    hidden={!this.state.showRequestOffline}
                    onDismiss={()=>{this.setValue("showRequestOffline", false)}}
                    title="Form has been postponed."
                >
                    As you are offline - the form will be sent as you reconnect to the internet.
                </Dialog> }
                {this.state.showRequestOfflineSent &&
                <Dialog
                    hidden={!this.state.showRequestOfflineSent}
                    onDismiss={()=>{this.setValue("showRequestOfflineSent", false)}}
                    title="Postponed form has been saved."
                >
                    Your previously prepared form has been succesfully saved.
                </Dialog> }                
                {this.state.showPleaseWait &&
                <Dialog 
                    hidden={!this.state.showPleaseWait}
                    onDismiss={()=>{this.setValue("showPleaseWait", false)}}
                    title="Sending..."
                    modalProps={{isBlocking:true}}
                >
                    <ProgressIndicator />                             
                </Dialog> }     
                {this.state.showValidationDialog &&
                <Dialog 
                    hidden={!this.state.showValidationDialog}
                    onDismiss={()=>{this.setValue("showValidationDialog", false)}}
                    title="Validation failed" 
                    minWidth={600}
                >
                    <p>Those fields are required:</p>
                    <ul>
                    {this.state.failedValidations.map(v=>{
                        return (<li>{v.nav} / {v.tab} / {v.field}</li>);
                    })}
                    </ul>
                </Dialog> }
            </div>
            );
    }

    getFieldRow(fieldProps:IFieldDefinition){

        if(fieldProps.isHidden){
            return <div></div>;
        }

        switch(fieldProps.type){
            case FieldType.text:
                return <TextFieldRow 
                            key={fieldProps.internalName} 
                            {...fieldProps} 
                            value={this.state.model[fieldProps.internalName]} 
                            onChanged={v=>this.onModelChanged(fieldProps.internalName,v)}/>;            
            case FieldType.longtext:
                return <LongTextFieldRow 
                            key={fieldProps.internalName} 
                            {...fieldProps} 
                            value={this.state.model[fieldProps.internalName]} 
                            onChanged={v=>this.onModelChanged(fieldProps.internalName,v)}/>;
            case FieldType.integer:
                return <NumberFieldRow 
                            key={fieldProps.internalName} 
                            {...fieldProps} 
                            value={this.state.model[fieldProps.internalName]} 
                            onChanged={v=>this.onModelChanged(fieldProps.internalName,v)}/>;
            case FieldType.dropdown:
                return <OptionsFieldRow 
                            key={fieldProps.internalName} 
                            {...fieldProps} 
                            value={this.state.model[fieldProps.internalName]} 
                            onChanged={v=>this.onModelChanged(fieldProps.internalName,v)} />
            case FieldType.date:
                return <DateFieldRow 
                            key={fieldProps.internalName} 
                            {...fieldProps} 
                            value={this.state.model[fieldProps.internalName]} 
                            onChanged={v=>this.onModelChanged(fieldProps.internalName,v)} />
            case FieldType.datetime:
                return <DateTimeFieldRow 
                            key={fieldProps.internalName} 
                            {...fieldProps} 
                            value={this.state.model[fieldProps.internalName]} 
                            onChanged={v=>this.onModelChanged(fieldProps.internalName,v)} />
            case FieldType.heading:
                return <HeadingRow 
                            key={fieldProps.internalName} 
                            {...fieldProps} 
                            value={this.state.model[fieldProps.internalName]} 
                            onChanged={v=>this.onModelChanged(fieldProps.internalName,v)} />
            case FieldType.downloadData:
                return <DownloadTextFieldRow 
                            key={fieldProps.internalName} 
                            {...fieldProps} 
                            onDownloadData={this.onDownloadData}
                            value={this.state.model[fieldProps.internalName]} 
                            onChanged={v=>this.onModelChanged(fieldProps.internalName,v)} />         
            case FieldType.incident:
                return <IncidentsRow 
                            key={fieldProps.internalName} 
                            {...fieldProps} 
                            value={this.state.model[fieldProps.internalName]} 
                            onChanged={v=>this.onModelChanged(fieldProps.internalName,v)} />             
            case FieldType.extraMSMonitored:
                return <ExtraMSMonitoredRow 
                            key={fieldProps.internalName} 
                            {...fieldProps} 
                            value={this.state.model[fieldProps.internalName]} 
                            onChanged={v=>this.onModelChanged(fieldProps.internalName,v)} />        
            default:
                return <p>This field failed to render</p>
        }

    }


    getFormForTab(fields:Array<IFieldDefinition>, tabName:string, navName:string){
        return (
        <div className={"ms-Grid "+styles.fullWidth} >
            {fields.map((f:IFieldDefinition)=>{return this.getFieldRow(f);})}            
            <div className={"ms-Grid-row "+styles.fieldRow}>           
                <div className={"ms-Grid-col ms-sm12 ms-md11 "+ styles.textRight} > 
                    <PrimaryButton      
                        className={styles.actionButtonMargin} 
                        iconProps={{iconName:"Send"}}           
                        text="Submit" 
                        allowDisabledFocus
                        onClick={this.onSubmit} />           
                    <PrimaryButton      
                        className={styles.actionButtonMargin}      
                        iconProps={{iconName:"SaveAs"}}      
                        text="Save draft" 
                        allowDisabledFocus
                        onClick={this.onSaveDraft} />   
                    <DefaultButton    
                        className={styles.actionButtonMargin}                  
                        text="Cancel" 
                        iconProps={{iconName:"Cancel"}}
                        allowDisabledFocus
                        onClick={this.onCancel} />                          
                </div>                   
            </div>
        </div>);
    }

}