import { ReactComponent } from "*.svg";
import React from "react";
import { Stack, TextField, PrimaryButton, Button, DefaultButton, Text, Nav, INavLink, Pivot, PivotItem, Label, FontIcon, IconButton, Icon, TooltipHost, ProgressIndicator, Dialog, DialogType } from "office-ui-fabric-react";

import styles from './../formstyles.module.css'; 
import IFormStructure, { ITabDefinition, IFieldDefinition, FieldType } from "./../Model/IFormStructure";
import HeadingRow from "./HeadingRow"; 
import OptionsFieldRow from "./OptionsRow";
import LongTextFieldRow from "./LongTextFieldRow";
import FormStructure from "../Model/FormStructure";
import IExtraMSMonitored from "../Model/IExtraMSMonitored";

export default class ExtraMSMonitoredRow extends React.Component<IFieldDefinition,any>{ 

    constructor(props:IFieldDefinition){
        super(props);

        this.state = {
            value: this.props.value ? this.props.value : null,
            extraMSMonitored: this.props.value ? this.props.value : []
        };

        this.setStateValue = this.setStateValue.bind(this);
        this.setValue = this.setValue.bind(this);
        this.addMS = this.addMS.bind(this);
        this.setMSValue = this.setMSValue.bind(this);
        this.removeMS = this.removeMS.bind(this);
    }

    componentWillReceiveProps(prevProps:IFieldDefinition){
        //TODO: reloading data
        let newState = {
            value: prevProps.value ? prevProps.value : null,
            extraMSMonitored: this.props.value ? this.props.value : []
        };
        this.setState(newState, this.forceUpdate);
    }

    setStateValue(fieldName:string, value:any){
        let ns = this.state as any;
        ns[fieldName] = value;
        this.setState(ns, this.forceUpdate);
    }

    setMSValue(fieldName:string, value:any, index:number){
        let ns = this.state.extraMSMonitored as any;
        ns[index][fieldName] = value;
        this.setState({extraMSMonitored:ns}, this.forceUpdate);

        this.setValue(ns);
    }
    
    setValue(value:any){
        let ns = this.state as any;
        ns.value = value; 
        this.setState(ns, this.forceUpdate);

        if(this.props.onChanged)
            this.props.onChanged(value);
    }

    addMS(){
        let inc = this.state.extraMSMonitored;
        inc.push(new IExtraMSMonitored());
        this.setState({extraMSMonitored:inc}, this.forceUpdate);
        this.setValue(inc);
    }

    removeMS(index:number){
        let inc = this.state.extraMSMonitored;
        inc.splice(index,1);

        this.setState({extraMSMonitored:inc}, this.forceUpdate);
        this.setValue(inc);
    }

    public render(): React.ReactElement<any> {
        return (
            <div className={"ms-Grid-row "+styles.fieldRow}>                     
                {this.state.extraMSMonitored.map((i:IExtraMSMonitored, index:number)=>{
                    return <div className={"ms-Grid-col ms-md12 ms-sm12"} 
                        key={'extraMSMonitored_'+(index+1)} >  
                        <OptionsFieldRow 
                            label={"Extra MS Monitored #"+(index+1)}
                            internalName="Extra MS Monitored" 
                            value={this.state.extraMSMonitored[index].country}
                            onChanged={((v:any)=>{this.setMSValue("country",v,index);}).bind(this)}
                            options={FormStructure.countries} 
                            isRequired={true} 
                            hasInfo={true} 
                            info={"@TODO"} 
                            type={FieldType.dropdown} />     
                        
                    </div>;
                })}      
                {this.state.extraMSMonitored.length>0 &&           
                <div className={"ms-Grid-col ms-sm6 ms-md5"}>
                    <DefaultButton   
                        iconProps={{iconName:"Cancel"}}
                        text="Remove MS Monitored"
                        onClick={(()=>this.removeMS(this.state.extraMSMonitored.length-1)).bind(this)}
                    />
                </div>}
                <div className={"ms-Grid-col ms-md6 ms-sm6 "+styles.textRight + (this.state.extraMSMonitored.length>0 ? "" : " ms-mdPush5")}> 
                    <PrimaryButton iconProps={{iconName:"Add"}} text="Add Extra MS Monitored" onClick={this.addMS}/> 
                </div>
            </div>
        );
    }
}