export default interface IFormStructure{
    Overview:Array<ITabDefinition>;
    "Pre-departure":Array<ITabDefinition>;
    "In-flight":Array<ITabDefinition>;
    "Arrival":Array<ITabDefinition>;
    "Return-flight":Array<ITabDefinition>;
    "Incident(s)":Array<ITabDefinition>;
    "Outcome":Array<ITabDefinition>;
}

export interface ITabDefinition{
    tab:string;
    isHidden?:boolean;
    fields:Array<IFieldDefinition>
}

export interface IFieldDefinition{
    value?:any;
    internalName:string;
    label:string;
    type:FieldType;
    hasInfo:boolean;
    info?:string;
    isRequired: boolean;
    onDownloadData?: (data:any)=>void;
    disbaled?:boolean;
    subformState?:any; //@TODO: this may be usefull for intelligent forms
    options?:Array<string>;
    onChanged?: (value:any)=>void;
    isHeading?: boolean;
    isHidden?: boolean;
}

export enum FieldType {
    text,
    dropdown,
    divider,
    date,
    time,
    integer,
    longtext,
    incident,
    downloadData,
    datetime,
    heading,
    extraMSMonitored
}