import { ReactComponent } from "*.svg";
import React from "react";
import { Stack, TextField, PrimaryButton, Button, DefaultButton, Text, Nav, INavLink, Pivot, PivotItem, Label, FontIcon, IconButton, Icon, TooltipHost, ProgressIndicator, Dialog, DialogType } from "office-ui-fabric-react";

import styles from './../formstyles.module.css'; 
import IFormStructure, { ITabDefinition, IFieldDefinition } from "../Model/IFormStructure";



export default class DownloadTextFieldRow extends React.Component<IFieldDefinition,any>{ 

    constructor(props:IFieldDefinition){
        super(props);
        this.state = {
            value: this.props.value ? this.props.value : "",
            showErrorDialog:false,
        }; 

        this.onDownload = this.onDownload.bind(this);
        this.hideErrorDialog = this.hideErrorDialog.bind(this);
    }

    componentWillReceiveProps(prevProps:IFieldDefinition){
        let newState = {
            value: prevProps.value ? prevProps.value : ""
        };
        this.setState(newState, this.forceUpdate);
    }

    setValue(value:any){
        let ns = this.state as any;
        ns.value = value.key; 
        this.setState(ns, this.forceUpdate);

        if(this.props.onChanged)
            this.props.onChanged(value);
    }

    onDownload(){
        let apiAddress = "https://www.google.com/";
        fetch(apiAddress).then(data=>{
            if(this.props.onDownloadData){
                this.props.onDownloadData(data);
            }
        }).catch(error=>{
            console.error(error);
            this.setState({showErrorDialog:true}, this.forceUpdate);
        });
    }

    hideErrorDialog(){
        this.setState({showErrorDialog:false}, this.forceUpdate);
    }

    public render(): React.ReactElement<any> {
        return (
            <div className={"ms-Grid-row "+styles.fieldRow}>           
                <div className={"ms-Grid-col ms-sm12 ms-md3"} >      
                    <Label>{this.props.label} {this.props.isRequired && <span className={styles.required}>*</span>}</Label>
                </div>        
                <div className={"ms-Grid-col ms-sm12 ms-md2 " +styles.marginBottom + " " +styles.downloadButtonContainer} >      
                    <PrimaryButton 
                        iconProps={{iconName:"Download"}}
                        allowDisabledFocus
                        onClick={this.onDownload} 
                    />
                </div>  
                <div className={"ms-Grid-col ms-sm12 ms-md6 "+styles.marginBottom} >      
                    <TextField  
                        disabled={this.props.disbaled}
                        value={this.state.value}
                        onChange={(e,v)=>{this.setValue(v)}}/>
                </div>     
                {this.props.hasInfo &&     
                <div className={"ms-Grid-col ms-sm12 ms-md1 " +styles.textRight} >      
                    <TooltipHost content={this.props.info}>
                        <Icon iconName="Info" className={"ms-IconExample " +styles.infoIcon} />
                    </TooltipHost>
                </div>}
                {this.state.showErrorDialog &&
                <Dialog
                    hidden={!this.state.showErrorDialog}
                    title="Failed to download data."
                    onDismiss={this.hideErrorDialog}
                >
                    Please check your internet connection or {this.props.label}
                </Dialog> }
            </div>
        );
    }
}