import { ReactComponent } from "*.svg";
import React from "react";
import { Stack, TextField, PrimaryButton, Button, DefaultButton, Text, Nav, INavLink, Pivot, PivotItem, Label, FontIcon, IconButton, Icon, TooltipHost, ProgressIndicator, Dialog, DialogType } from "office-ui-fabric-react";

import styles from './../formstyles.module.css'; 
import IFormStructure, { ITabDefinition, IFieldDefinition } from "../Model/IFormStructure";



export default class NumberFieldRow extends React.Component<IFieldDefinition,any>{ 

    constructor(props:IFieldDefinition){
        super(props);
        this.state = {
            value: this.props.value ? this.props.value : ""
        }; 

        this.setValue = this.setValue.bind(this);
    }

    componentWillReceiveProps(prevProps:IFieldDefinition){
        let newState = {
            value: prevProps.value ? prevProps.value : ""
        };
        this.setState(newState, this.forceUpdate);
    }

    setValue(value:any){
        let ns = this.state as any;
        let oldValue = this.state.value+'';

        if(isNaN(value)){
            this.setState({value:oldValue}, this.forceUpdate);
        } else {            
            let integer = parseInt(value);

            ns.value = integer;
            this.setState(ns, this.forceUpdate);

            if(this.props.onChanged)
                this.props.onChanged(integer);
        }
    }

    public render(): React.ReactElement<any> {
        return (
            <div className={"ms-Grid-row "+styles.fieldRow}>           
                <div className={"ms-Grid-col ms-sm12 ms-md3"} >      
                    <Label>{this.props.label} {this.props.isRequired && <span className={styles.required}>*</span>}</Label>
                </div>  
                <div className={"ms-Grid-col ms-sm12 ms-md3 "+styles.marginBottom} >      
                    <TextField  
                        disabled={this.props.disbaled}
                        value={this.state.value}
                        onChange={(e,v)=>{this.setValue(v)}}/>
                </div>     
                {this.props.hasInfo &&     
                <div className={"ms-Grid-col ms-sm12 ms-md6 " +styles.textRight} >      
                    <TooltipHost content={this.props.info}>
                        <Icon iconName="Info" className={"ms-IconExample " +styles.infoIcon} />
                    </TooltipHost>
                </div>}
            </div>
        );
    }
}