import { ReactComponent } from "*.svg";
import React from "react";
import { Stack, TextField, PrimaryButton, Button, DefaultButton, Text, Nav, INavLink, Pivot, PivotItem, Label, FontIcon, IconButton, Icon, TooltipHost, ProgressIndicator, Dialog, DialogType, MessageBar } from "office-ui-fabric-react";

import styles from './../formstyles.module.css'; 
import IFormStructure, { ITabDefinition, IFieldDefinition } from "../Model/IFormStructure";



export default class HeadingRow extends React.Component<IFieldDefinition,any>{ 

    constructor(props:IFieldDefinition){
        super(props);
        this.state = { 
        }; 
    }

    public render(): React.ReactElement<any> {
        return (
            <div className={"ms-Grid-row "+styles.fieldRow}>           
                {!this.props.hasInfo && 
                <div className={"ms-Grid-col ms-sm12 ms-md11"} >       
                    <MessageBar>{this.props.label}</MessageBar>
                </div>}
                {this.props.hasInfo &&     
                <div className={"ms-Grid-col ms-sm12 ms-md11 "} >      
                    <TooltipHost content={this.props.info}>
                        <MessageBar>{this.props.label}</MessageBar>
                    </TooltipHost>
                </div>}
            </div>
        );
    }
}