export default class FormRules{

    runRules(model:object):object{

        /*
        //Examples: 
        model = this.setValueIf((model)=>{ return (model as any)["Some Field #1 Internal name"]!=null && (model as any)["Some Field #1 Internal name"] == 'XYZ'; }, 
                            "Some Field #3",
                            model, 
                            "New value");
        */

        //3. Overview / Mission identity / Field: Country #1 (If Type = CRO then set field value to value of field CoR collecting.)
        let check3 = (model:any)=>{ return (model as any)["Operation type"]!=null && (model as any)["Operation type"] === 'CRO'; };
        model = this.setValueIf(check3, "Country #1", model, (model as any)["CoR collecting"]);

        //8. Overview / Other states and participants field: Country name (2 If Type = CRO then set field value of field CoR collecting.) 
        let check8 = (model:any)=>{ return (model as any)["Operation type"]!=null && (model as any)["Operation type"] === 'CRO'; };
        model = this.setValueIf(check8, "osap_Country name", model, (model as any)["CoR collecting"]);

        return model;
    }



    private setValueIf(condition:(model:object)=>boolean, fieldInternalNameToChangeValue:string, model:object, newValue:any){

        if(condition(model) && (model as any)[fieldInternalNameToChangeValue]!=null){
            (model as any)[fieldInternalNameToChangeValue] = newValue;
        }

        return model;
    }

}