import { ReactComponent } from "*.svg";
import React from "react";
import { Stack, TextField, PrimaryButton, Button, DefaultButton, Text, Nav, INavLink, Pivot, PivotItem, Label, FontIcon, IconButton, Icon, TooltipHost, ProgressIndicator, Dialog, DialogType, DatePicker, DayOfWeek, IDropdownOption, Dropdown } from "office-ui-fabric-react";

import styles from './../formstyles.module.css'; 
import IFormStructure, { ITabDefinition, IFieldDefinition } from "../Model/IFormStructure";



export default class DateTimeFieldRow extends React.Component<IFieldDefinition,any>{ 


    constructor(props:IFieldDefinition){
        super(props);

        let d=null, h=null, m=null;
        
        if(this.props.value){
            d = this.props.value.split(' ')[0];            
            h = this.props.value.split(' ')[1].split(':')[0];
            m = this.props.value.split(' ')[1].split(':')[1];
        }

        let dSplitted=[];
        let dAsDate = null;
        if(d!=null){
            dSplitted = d.split('-');
            dAsDate  = new Date(dSplitted[0],dSplitted[1],dSplitted[2]);
        }
        

        this.state = {
            hoursModel:{
                key: this.props.value ? h:"00",
                text: this.props.value ? h:"00",
            },
            minutesModel:{
                key: this.props.value ? m:"00",
                text: this.props.value ? m:"00",
            },
            dateModel: this.props.value ? dAsDate:"",
            value: this.props.value ? this.props.value : "",
            availableHours: this.getAvailableHours(),
            availableMinutes: this.getAvailableMinutes(),
        };

        this.setStateValue = this.setStateValue.bind(this);
        this.setDateValue = this.setDateValue.bind(this);
        this.setHourValue = this.setHourValue.bind(this);
        this.setMinuteValue = this.setMinuteValue.bind(this);
        this.onFormatDate = this.onFormatDate.bind(this);
        this.addExtraZero = this.addExtraZero.bind(this);
    }

    
    componentWillReceiveProps(prevProps:IFieldDefinition){

        let d=null, h=null, m=null;
        
        if(prevProps.value){
            d = prevProps.value.split(' ')[0];            
            h = prevProps.value.split(' ')[1].split(':')[0];
            m = prevProps.value.split(' ')[1].split(':')[1];
        }

        
        let dSplitted=[];
        let dAsDate = null;
        if(d!=null){
            dSplitted = d.split('-');
            dAsDate  = new Date(dSplitted[0],dSplitted[1],dSplitted[2]);
        }
        

        let newState = {
            hoursModel:{
                key: prevProps.value ? h:"00",
                text: prevProps.value ? h:"00",
            },
            minutesModel:{
                key: prevProps.value ? m:"00",
                text: prevProps.value ? m:"00",
            },
            dateModel: prevProps.value ? dAsDate:"",
            value: prevProps.value ? prevProps.value : "",
        };
        this.setState(newState, this.forceUpdate);
    }

    getAvailableHours():Array<IDropdownOption>{
        let result = Array<IDropdownOption>();
        
        for(let i=0;i<24;i++){
            let model = {key:i.toString(), text:i.toString()};
            if(i<10){
                model.key='0'+model.key;
                model.text='0'+model.text;
            }
            result.push(model);
        }
        return result;
    }

    getAvailableMinutes():Array<IDropdownOption>{
        let result = Array<IDropdownOption>();
        
        for(let i=0;i<60;i++){
            let model = {key:i.toString(), text:i.toString()};
            if(i<10){
                model.key='0'+model.key;
                model.text='0'+model.text;
            }
            result.push(model);
        }
        return result;
    }


    setStateValue(fieldName:string, value:any){
        let ns = this.state as any;
        ns[fieldName] = value;
        this.setState(ns, this.forceUpdate);
    }

    
    setValue(date?:any, hour?:any, minute?: any){ 
        let value = ''; 
        let d = this.state.dateModel;
        if(date){
            this.setState({dateModel:date});
            d=date;
        }

        let h = this.state.hoursModel.text;
        if(hour){ 
            this.setState({hoursModel:hour});
            h=hour.text;
        }

        let m = this.state.minutesModel.text;
        if(minute){
            this.setState({minutesModel:minute});
            m=minute.text;
        }

        value=`${this.onFormatDate(d)} ${h}:${m}`;
        this.setState({value:value}, this.forceUpdate);

        if(this.props.onChanged)
            this.props.onChanged(value);
    }

    addExtraZero(num:any){
        if(num == null) return '';

        if(num<10) num = '0'+num;
        return num;
    }


    onFormatDate(date?: Date): string{    
        if(typeof date == "string") date = new Date(date);

        if(date && date.toDateString()!="Invalid Date"){
            console.log(date);
            return date.getFullYear() +'-'+ this.addExtraZero(date.getMonth() + 1) +'-'+ this.addExtraZero(date.getDate());
        } else {
            return "";
        }
    }

    setDateValue(v:any){
        this.setValue(v,null,null);
    }
    setHourValue(e:any,v:any){
        this.setValue(null,v,null);
    }
    setMinuteValue(e:any,v:any){
        this.setValue(null,null,v);
    }

    public render(): React.ReactElement<any> {
        return (
            <div className={"ms-Grid-row "+styles.fieldRow}>           
                <div className={"ms-Grid-col ms-sm12 ms-md3"} >      
                    <Label>{this.props.label} {this.props.isRequired && <span className={styles.required}>*</span>}</Label>
                </div>  
                <div className={"ms-Grid-col ms-sm12 ms-md4 " +styles.marginBottom} >      
                    <DatePicker   
                        disabled={this.props.disbaled}
                        firstDayOfWeek={DayOfWeek.Monday}
                        value={this.state.dateModel}                 
                        formatDate={this.onFormatDate}       
                        onSelectDate={this.setDateValue}/>
                </div>  
                <div className={"ms-Grid-col ms-sm6 ms-md2 "+styles.marginBottom}>  
                    <Dropdown 
                        disabled={this.props.disbaled}
                        options={this.state.availableHours}
                        selectedKey={this.state.hoursModel.key}
                        onChange={this.setHourValue}/>
                </div>   
                <div className={"ms-Grid-col ms-sm6 ms-md2 "+styles.marginBottom}>     
                    <Dropdown 
                        disabled={this.props.disbaled}
                        options={this.state.availableMinutes}
                        selectedKey={this.state.minutesModel.key}
                        onChange={this.setMinuteValue}/>
                </div>    
                {this.props.hasInfo &&     
                <div className={"ms-Grid-col ms-sm12 ms-md1 " +styles.textRight} >      
                    <TooltipHost content={this.props.info}>
                        <Icon iconName="Info" className={"ms-IconExample " +styles.infoIcon} />
                    </TooltipHost>
                </div>}
            </div>
        );
    }
}