import React from "react";
import { Label,Icon, TooltipHost, Dropdown, IDropdownOption } from "office-ui-fabric-react";
import styles from './../formstyles.module.css'; 
import { IFieldDefinition } from "./../Model/IFormStructure";



export default class OptionsFieldRow extends React.Component<IFieldDefinition,any>{ 

    constructor(props:IFieldDefinition){
        super(props);
        this.state = {
            value: this.props.value ? this.props.value : "",
            valueModel:{
                key: this.props.value ? this.props.value : "",
                text: this.props.value ? this.props.value : "",
                isSelected:true,
            }, 
            options: this.mapOptions(props),
        };

        this.setValue = this.setValue.bind(this);
        this.mapOptions = this.mapOptions.bind(this);
    }

    componentWillReceiveProps(prevProps:IFieldDefinition){
        let newState = {
            value: prevProps.value ? prevProps.value : "",
            valueModel:{
                key: prevProps.value ? prevProps.value : "",
                text: prevProps.value ? prevProps.value : "",
                isSelected:true,
            }, 
            options: this.mapOptions(prevProps),
        };
        this.setState(newState, this.forceUpdate);
    }

    setValue(value:any){
        let ns = this.state as any;
        ns.value = value.key;
        ns.valueModel = value; 
        this.setState(ns, this.forceUpdate);

        if(this.props.onChanged)
            this.props.onChanged(value.key);
    }

    mapOptions(p:IFieldDefinition):Array<IDropdownOption>{
        if(p && p.options){
            let res = p.options.map((o:string)=>{
                return {key:o, text:o, isSelected:false} as IDropdownOption;
            });
            if((this.props.value == undefined || this.props.value == "") && res.length>0)
                res[0].isSelected = true;
            return res;
        } else return new Array<IDropdownOption>();
    }

    public render(): React.ReactElement<any> {
        return (
            <div className={"ms-Grid-row "+styles.fieldRow}>           
                <div className={"ms-Grid-col ms-sm12 ms-md3"} >      
                    <Label>{this.props.label} {this.props.isRequired && <span className={styles.required}>*</span>}</Label>
                </div>  
                <div className={"ms-Grid-col ms-sm12 ms-md8 "+styles.marginBottom} >      
                    <Dropdown  
                        disabled={this.props.disbaled}
                        options={this.state.options}
                        selectedKey={this.state.valueModel.key}
                        onChange={(e,v)=>{this.setValue(v)}}/>
                </div>     
                {this.props.hasInfo &&     
                <div className={"ms-Grid-col ms-sm12 ms-md1 " +styles.textRight} >      
                    <TooltipHost content={this.props.info}>
                        <Icon iconName="Info" className={"ms-IconExample " +styles.infoIcon} />
                    </TooltipHost>
                </div>}
            </div>
        );
    }
}